.container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color:lavender;
  }
  
  /* Styles for the h3 elements */
  h3 {
    font-size: 20px;
    margin-bottom: 15px;
  }
  
  /* Styles for the buttons */
  button {
    padding: 10px 20px;
    margin-top: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  button:hover {
    background-color: #ddd;
  }
  
  /* Styles for the login button */
  .login-btn {
    background-color: #0e7496;
    color: white;
  }
  
  .login-btn:hover {
    background-color: #0d673a;
  }
  
  /* Styles for the logout button */
  .logout-btn {
    background-color: #423d3c;
    color: white;
  }
  
  .logout-btn:hover {
    background-color: #d32f2f;
  }
  .details-block {
    margin-bottom: 20px;
  }